<template>
    <input class="base" 
            :placeholder="placeholder"
            :type="type" 
            :class="innerClass"
            :style="{height:normalizedSize.H,width:normalizedSize.W}"
            @input="$emit('input',$event.target.value)"
            @focus="onFocus"
            @blur="onBlur"
    />
</template>
<script>
export default {
    name : "Button",
    props : {
        value : String,
        placeholder : String,
        styleClass : {
            type : String,
            default : 'default'
        },
        type : {
            type : String,
            default : 'button'
        },
        size : {
            type : Object,
            default : ()=>{
                return {
                    H : 30,
                    W : 160
                }
            }
        }
    },
    data(){
        return {
            innerClass : this.styleClass
        }
    },
    computed :{
        normalizedSize(){
            let size = Object.assign(this.size);
            if(typeof size.H == 'number')size.H += 'px';
            if(typeof size.W == 'number')size.W += 'px';
            return size;
        }
    },
    methods : {
        onFocus(){
            //switch to active style
            this.innerClass += ' '+this.innerClass+'-active'
        },
        onBlur(){
            //switch to inactivated style
            this.innerClass = this.styleClass
        }
    }
}
</script>
<style scoped>
.base{
    /* 去掉默认样式 */
    box-shadow: none;
    text-shadow: none;
    border : none;
    outline: none;
    text-decoration: none;
    background : white;
    /* 基础样式 */
    border-radius: 5px;
    color: black
}
.default {
    border : 1px solid black;
    color : black;
}
.default-active{
    box-shadow: 0px 0px 5px 1px black;
}
.primary{
    border : 1px solid #155A90;
}
.primary-active{
    box-shadow: 0px 0px 5px 1px #155A90;
}
.success{
    border : 1px solid #588E0D;
}
.success-active{
    box-shadow: 0px 0px 5px 1px #588E0D;
}
.warning{
    border : 1px solid #D6640F;
}
.warning-active{
    box-shadow: 0px 0px 5px 1px #D6640F;
}
.error{
    border : 1px solid #9D0812;
}
.error-active{
    box-shadow: 0px 0px 5px 1px #9D0812;
}
</style>
