<template>
  <div id="app" :style="{height:H+'px'}">
    <div id="header">
        <Header>
          <div class="headerItem">
              <img  src="./assets/logo.jpg" width="50" height="50"/>
          </div>
          <div id="search" class="headerItem">
              <Input
                    type="text" 
                    styleClass="primary"
                    placeholder="搜索..."
                    @input="search"
              /> 
              <div style="height:25px;width:50px"></div>
          </div>
          <div class="headerItem" v-if="!state.isLogin">
              <span @click="openSignInWindow">登陆</span> |
              <span @click="openSignUpWindow">注册</span>
          </div>
          <div class="headerItem" v-if="state.isLogin" style="height:20px;flex-basis:130px;">
              <span style="color:#007FFF"
              >
                <div 
                  @mouseover="activeMenu"
                >
                  <strong>{{user.nickname || user.uId}}</strong>
                </div>
                <Menu
                  v-if="showMenu"
                  :list = "menu"
                  @mouseover.native="selectMenu"
                  @redirectTo = "handleMenu"
                ></Menu>
              </span>
          </div>
        </Header>
        <Nav 
          :tabs = "tabs"
          @switchChange = "flashPanel"
        ></Nav>
    </div>
    <div id="contentBody">
        <Panel 
            v-for = "(ques,index) in questions" :key="index"
            :id= "ques.id"
            :isTop= "ques.isTop"
            :category= "ques.category"
            :name = "ques.name"
            :time = "ques.time"
            :question= "ques.question" 
            :followNum= "ques.followNum"
            :starNum= "ques.starNum"
        ></Panel>
    </div>
    <div id="suspen">
      <Suspen @mouseover.native="activeEdit"></Suspen>
      <div 
        id="edit-panel"
        v-if="showEdit"
        @mouseover="selectEdit"
      >
        <Triangle
        ></Triangle>
        <div id="edit-content">
            <Textarea v-model="editQues.text"></Textarea>{{editQues.text}}
            <Select v-model="editQues.category"></Select>{{editQues.category}}
            <Button
              @click="publishQues"
              text = "发布"
            ></Button>
        </div>
      </div>
    </div>
    <Popup
       :title="popup.title"
       :isActive="popup.isOpen"
       @stateChange = "cannelPopup"
    >
        <form v-if="popup.isSignIn">
          <div id="form">
            <div>
                <label>邮箱</label>
            </div>
            <div>
              <Input 
                  type="email"
                  v-model="account"
                  :size = "{
                     H : 35,
                     W : 200
                  }" 
              />
            </div>
            <div>
              <label>密码</label>
            </div>
            <div>
              <Input 
                  type="text" 
                  v-model="password"
                  :size = "{
                     H : 35,
                     W : 200
                  }" 
              />
            </div>
            <div id="item">
              <Button  
                  styleClass="default"
                  @click="signIn"
                  :size="{
                       H : 50,
                       W : 300
                    }"
                  text="登录"
                  @ click="signIn"
              ></Button>
            </div>
          </div>
        </form>
        <form v-else>
          <div id="form">
            <div>
                <label>邮箱</label>
            </div>
            <div>
              <Input 
                  type="email" 
                  placeholder="输入常用邮箱"
                  v-model="account"
                  :size = "{
                     H : 35,
                     W : 200
                  }" 
              />
            </div>
            <div>
              <label>密码</label>
            </div>
            <div>
              <Input 
                  type="text" 
                  placeholder="字母、数字、符号组合"
                  v-model="password"
                  :size = "{
                     H : 35,
                     W : 200
                  }" 
              />
            </div>
            <div id="item">
              <Button  
                  @click="signUp"
                  :size="{
                       H : 50,
                       W : 300
                    }"
                  text="注册"
              ></Button>
            </div>
          </div>
        </form>
    </Popup>
  </div>
</template>
<style>
