<script>
import Popup from './components/Popup.vue'
import Button from './components/Button.vue';
import Input from './components/Input.vue';
import Header from './components/Header.vue';
import Nav from './components/Nav.vue';
import Panel from './components/Panel.vue';
import Menu from './components/Menu.vue';
import Suspen from './components/Suspen.vue';
import Triangle from './components/Triangle.vue';
import Select from './components/Select.vue';
import Textarea from './components/Textarea.vue';
import {transformRequest,getPageSize} from './common/function.js';
//global value 
let QUES = []
let QUESTOP = []
let QUESUNTOP = []
//axios params factory-func
function axiosPostParamsGenerator(url,data){
  return {
      url:url,
      method : 'post',
      data : data,
      transformRequest: [function (data) {
        // Do whatever you want to transform the data
        return transformRequest(data);
      }],
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
  }      
}
export default {
  name: 'app',
  components: {
    Popup,
    Button,
    Input,
    Header,
    Nav,
    Panel,
    Menu,
    Suspen,
    Triangle,
    Textarea,
    Select
  },
  data : function(){
    return {
       H : 0,
       W : 0,
       account : '',
       password : '',
       popup : {
         title : '登录',
         isOpen : true,
         isSignIn : true
       },
       menu : ['收藏的提问','回答的提问','我的提问','个人主页','退出'],
       menuTime : null,
       showMenu : false,
       showEdit : false,
       editTime : null,
       tabs : [
         'computer' ,'pe','math','all'
       ],
       questions : [
          {
            id : 0,
            name : '熊饲',
            isTop : true,
            category : 'vue',
            time : '2019-07-5 12:57',
            question : 'Vue-cli 3.x + axios 跨域方案踩坑指北',
            followNum : 120,
            starNum : 300
          }
       ],
       state :{
         isOk : true,
         isLogin : false
       },
       editQues : {
         text : '',
         category : ''
       },
       user : {}
    }
  },
  methods : {
    openSignInWindow(){
       this.popup = {
         title : '登录',
         isOpen : true,
         isSignIn : true
       }
    },
    cannelPopup(state){
      this.popup.isOpen = state;
    },
    openSignUpWindow(){
      this.popup = {
         title : '注册',
         isOpen : true,
         isSignIn : false
      }
    },
    activeMenu(){
      this.showMenu = true;
      this.menuTime = setTimeout(()=>{
        this.showMenu = false; 
      },1000*1)
    },
    selectMenu(){
      clearTimeout(this.menuTime);
      this.menuTime = setTimeout(()=>{
        this.showMenu = false;
      },1000*3);
    },
    activeEdit(){
      this.showEdit = true;
      this.editTime = setTimeout(()=>{
        this.showEdit = false;
      },1000*1);
    },
    selectEdit(){
      clearTimeout(this.editTime);
      this.editTime = setTimeout(()=>{
        this.showEdit = false;
      },1000*3);
    },
    inactiveMenu(){
      this.showMenu = false;
    },
    signIn(){
       this.axios(axiosPostParamsGenerator('/api/login',{
          id: this.account,
          pwd: this.password
       })).then(res=>{
         console.log(res);
         res = res.data;
         if(res.code == 'success'){
           console.log('in')
           //TODO : close popup and show login-in state
           this.popup.isOpen = false;//close popup
          //  this.$set(this.popup , 'isOpen' , false);
           this.state.isLogin = true;
           this.user = {
             uId : res.uId,
             nickname : res.nickname,
             isOk : res.isOk
           }
         }else if(res.code == 'fail'){
           //TODO : alert login failed
         }else{
           //TODO : alert unknown error
         }
       })
    },
    signUp(){
      this.axios(axiosPostParamsGenerator('/api/Register',{
        id : this.account,
        psd : this.password
      })).then(res=>{
        console.log(res);
        if(res.code == 'success'){
           //TODO : close popup and show login-in state
           this.popup.isOpen = false;//close popup
           this.state.isLogin = false;
         }else if(res.code == 'fail'){
           //TODO : alert login failed
         }else{
           //TODO : alert unknown error
         }
      })
    },
    handleMenu(index){
      const storeState = ()=>{
          localStorage.setItem('popup',JSON.stringify(this.popup));
          localStorage.setItem('state',JSON.stringify(this.state));
          localStorage.setItem('user',JSON.stringify(this.user));
      }
      switch(this.menu[index]){
        case '退出' :
          this.axios.get('/api/logout').then(res=>{
            console.log(res);
            if(res.data.code == 'success')
              this.popup.isOpen = true;
              this.state.isLogin = false;
              localStorage.removeItem('popup');
              localStorage.removeItem('state');
              localStorage.removeItem('user');
          })
          break;
        case '收藏的提问' : 
          //use Web-storage to store state which will be used in the future
          storeState();
          //redirect 
          window.location.href = `/api/manageCollect?uId=${this.user.uId}`;
          break;
        case '回答的提问' :
          storeState();
          window.location.href = `/api/ViewUsrAnswer?uId=${this.user.uId}`
          break;
        case '我的提问' :
          storeState();
          window.location.href = `/api/ViewUsrQues?uId=${this.user.uId}`
          break;
        case '个人主页' :
          storeState();
          window.location.href = `/api/ViewFriendList?uId=${this.user.uId}`
          break;
      }
    },
    flashPanel(index){
      //TODO : fetch data from Back-End 
      //according to different index reflecting different category
      let category = this.tabs[index];
      this.questions = category=='all'?QUES: QUES.filter(item=>{
        return item.category==category;
      })
    },
    search(data){
      //TODO: fetch questions which match the data param
      //preprocess data to regexp
      data = data.split("").map(item=>{
         return ['\\','^','$','*','+','?','{','}'
         ,'.','(',')',':','=','!','<','>',
         '|','[',']'].includes(item) ? '\\'+item:item;
      }).join("");
      let r = new RegExp(data);
      this.questions = QUES.filter(item=>{
        return r.exec(item.question);
      })
    },
    publishQues(){
      let uId = this.user.uId;
      let {text , category} = this.editQues;
      console.log(this.editQues)
      let editQues = this.editQues;
      let quesitons = this.questions;
      this.axios(axiosPostParamsGenerator('/api/quesEdit',{
         text,
         category,
         uId 
      })).then(res=>{
        console.log(res)
        res = res.data
        if(res){
          // question.concat()
          let question = {
            id : parseInt(res.quesId),
            name : res.nickname,
            isTop : res.isTop?true:false,
            category : res.category,
            time : res.subTime,
            question : res.text,
            followNum : res.follow,
            starNum : res.starNum
          }
          //重新部署ques
          question.isTop? QUESTOP.unshift(question):QUESUNTOP.unshift(question);
          this.questions = QUESTOP.concat(QUESUNTOP);
        }
      })
    }
  },
  
  created(){
     this.H = getPageSize().pageHeight;
     //recovery state

     let popup = JSON.parse(localStorage.getItem('popup'));
     let state = JSON.parse(localStorage.getItem('state'));
     let user = JSON.parse(localStorage.getItem('user'));
     this.popup = popup || this.popup;
     this.state = state || this.state;
     this.user = user || this.user;
     console.log(this.popup)
     //TODO : fetch data of questions belonging to hot to init page
     function handleQues(questionList){
         let questions = []
          questionList.forEach(item=>{
         let {account,category,clickNum,follow,is_top,subTime,text,qid} = item;
         let newItem = {
            id : parseInt(qid),
            name : account,
            isTop : is_top<=0?false:true,
            category : category,
            time : subTime,
            question : text,
            followNum : follow,
            starNum : clickNum
         }
         questions.push(newItem);
       })
       return questions;
     }
     this.axios.get('/api/index').then(res=>{
       console.log(res.data);
       QUESTOP = [].concat(handleQues(res.data.feedTop))
       QUESUNTOP = [].concat(handleQues(res.data.feed))
       QUES = QUESTOP.concat(QUESUNTOP);//save a cache
       this.questions = QUES;
     })
  },
  destroyed(){
      //delete web-storage
      // localStorage.removeItem('popup');
      // localStorage.removeItem('state');
      // localStorage.removeItem('user');
  }

}
</script>
