<template>
    <div id="textarea">
        <textarea 
            id="content"
            @input ="$emit('input',$event.target.value)"
        ></textarea>
    </div>
</template>
<style scoped>
#textarea{

}
#content{
    width : 100%;
    height: 200px;
}
</style>
<script>
export default {
    props : {
        value : String
    }
}
</script>




