<template>
    <div id="nav">
        <div 
            class ="tab" 
            v-for = "(val,index) in dataTabs" 
            :key="index"
            @click="switchTo(index)"
        >
            <div>{{val}}</div>
        </div>
    </div>
</template>
<style scoped>
#nav{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 100%;
    box-shadow: 10px 10px 10px 1px #C8C8C8 ;
}
.tab{
    flex-basis: 150px;
}
</style>
<script>
export default {
    name : "Header",
    props : {
       tabs : {
           type : Array,
           required : true,
           default : ()=>{
               return ['置顶','全部']
           }
       }
    },
    data : function(){
        return {
            dataTabs : this.tabs
        }
    },
    computed :{

    },
    methods : {
        switchTo(index){
            this.$emit('switchChange',index);
        }
    }
}
</script>

