<template>
    <div id="triangle" :style="{borderTop:size[0],borderRight:size[1],borderBottom:size[2],borderLeft:size[3]}">
    </div>
</template>
<style scoped>
#triangle{
    width : 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 40px solid #CECECE;
}
</style>
<script>
export default {
    props : {
        size : {
            type : Array,
            default : ()=>{
                return [
                    'none',
                    '20px solid transparent',
                    '20px solid #CECECE',
                    '20px solid transparent'
                ]
            }
        }
    }
}
</script>





